$boxes-color:#eee;
$boxes-width: 100px;
$boxes-in-row: 7;
$padding-size: 10px;
$gap-size: 1px;
$block-width: $boxes-width * $boxes-in-row + $gap-size * 7 + $padding-size * 2;
$days: mon, tue, wed, thu, fri, sat, sun;
$week:
  "mon" 1,
  "tue" 2,
  "wed" 3,
  "thu" 4,
  "fri" 5,
  "sat" 6,
  "sun" 7;
